import React, { useEffect, useState } from 'react'
import { Button } from '@material-ui/core'
import { SubmitHandler, useForm } from 'react-hook-form'
import { useNavigate, useLocation } from 'react-router-dom'
import { yupResolver } from '@hookform/resolvers/yup'
import { onboardingExperienceSchema } from '../../../../utils/schemas'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import {
  Cohort,
  Course,
  Education,
  Experience,
  ExperienceInfo,
} from '../../../../services/types/onboarding/onboarding-types'
import { getOnboardingBaseData } from '../../../../services/axios/onboarding'
import { setExperienceInfo } from '../../../../services/slices/onboarding-slice'
import { ControlledCourseField, ControlledSelectField, ControlledTextField } from '../../../Sub/FormElements'
import useStyles from './OnboardingFormExperience.styles'

const OnboardingFormExperience = () => {
  const classes = useStyles()
  const navigate = useNavigate()
  const location = useLocation()
  const dispatch = useAppDispatch()

  const avatar = location.state?.avatar
  const { experienceInfo, personalInfo, baseData } = useAppSelector((store) => store.onboarding)

  const defaultValues = experienceInfo || {
    experience_type: '',
    previous_job_position: '',
    education_type: '',
    institution: '',
  }

  const {
    control,
    handleSubmit,
    register,
    setValue,
    watch,
    getValues,
    formState: { errors },
  } = useForm<ExperienceInfo>({
    defaultValues,
    resolver: yupResolver(onboardingExperienceSchema),
    mode: 'onChange',
  })

  const courseValue = watch('course_id')
  const degreeValue = watch('education_type')
  const experienceValue = watch('experience_type')
  const cohortValue = watch('cohort_number')
  const [courses, setCourses] = useState<Course[]>([])
  const [education, setEducation] = useState<Education[]>([])
  const [experience, setExperience] = useState<Experience[]>([])
  const [cohort, setCohort] = useState<Cohort[]>([])

  useEffect(() => {
    if (!personalInfo) {
      navigate('/onboarding/personal-info')
    }
  }, [personalInfo])

  useEffect(() => {
    if (!baseData) dispatch(getOnboardingBaseData())
    else {
      const sortedCourses = [...baseData.courses].sort((a, b) => {
        if (a.name < b.name) {
          return -1
        }
        if (a.name > b.name) {
          return 1
        }
        return 0
      })

      if (baseData?.courses) setCourses(sortedCourses)
      if (baseData?.education) setEducation(baseData.education)
      if (baseData?.experience) setExperience(baseData.experience)
    }
  }, [baseData])

  useEffect(() => {
    register('course_id')
    register('education_type')
    register('cohort_number')
    register('experience_type')
  }, [register])

  useEffect(() => {
    if (courseValue) {
      setCohortsByCourse(courseValue?.id)
    }
  }, [courseValue])

  const handleChangeDegree = (e: React.ChangeEvent<{ name?: string | undefined; value: string }>) => {
    setValue('education_type', e.target.value, { shouldValidate: true, shouldDirty: true })
  }

  const handleChangeExperience = (e: React.ChangeEvent<{ name?: string | undefined; value: string }>) => {
    setValue('experience_type', e.target.value, { shouldValidate: true, shouldDirty: true })
  }

  useEffect(() => {
    setCohortsByCourse(courseValue?.id)
  }, [courseValue])

  const handleChangeCohort = (e: React.ChangeEvent<{ name?: string | undefined; value: number }>) => {
    setValue('cohort_number', e.target.value, { shouldValidate: true, shouldDirty: true })
  }

  const setCohortsByCourse = (courseId: string | number) => {
    const choosenCourse = courses.find((item) => item.id === +courseId)
    const cohortsInCourse = choosenCourse?.cohorts
    if (!cohortsInCourse) setCohort([])
    else setCohort([...cohortsInCourse].sort((a, b) => +a?.cohort_number - +b?.cohort_number))
  }

  const onSubmit: SubmitHandler<ExperienceInfo> = (formData) => {
    dispatch(setExperienceInfo(formData))
    navigate('/onboarding/resume', { state: { avatar } })
  }

  return (
    <>
      <form onSubmit={handleSubmit(onSubmit)} className={classes.form}>
        <fieldset className={classes.fieldset}>
          <ControlledCourseField
            // TODO разобрать с тс-игнорами ниже
            control={control}
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            // @ts-ignore
            error={!!errors?.course_id?.message}
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            // @ts-ignore
            helperText={errors?.course_id?.message}
            width={386}
            name="course_id"
          />
          <ControlledSelectField
            labelTitle="Номер потока"
            tipContent="Выбери поток"
            value={cohortValue}
            error={!!errors?.cohort_number?.message}
            helperText={errors?.cohort_number?.message}
            onChange={handleChangeCohort}
            items={cohort?.map((c) => ({ ...c, name: c?.cohort_number }))}
            maxWidth={386}
            disabled={!courseValue?.id}
          />
          <ControlledSelectField
            labelTitle="Опыт работы"
            tipContent="Опыт работы"
            value={experienceValue}
            error={!!errors?.experience_type?.message}
            helperText={errors?.experience_type?.message}
            onChange={handleChangeExperience}
            items={experience}
            maxWidth={386}
          />
          <ControlledTextField
            labelTitle="Должность на предыдущем месте работы"
            fieldName="previous_job_position"
            placeholder="Должность на предыдущем месте работы"
            control={control}
            error={!!errors.previous_job_position?.message}
            helperText={errors.previous_job_position?.message}
            maxWidth={386}
          />
          <ControlledSelectField
            labelTitle="Тип образования"
            tipContent="Тип образования"
            value={degreeValue}
            error={!!errors?.education_type}
            helperText={errors?.education_type?.message}
            onChange={handleChangeDegree}
            items={education}
            maxWidth={386}
          />
          <ControlledTextField
            labelTitle="Учебное заведение"
            fieldName="institution"
            placeholder="Учебное заведение"
            control={control}
            error={!!errors.institution?.message}
            helperText={errors.institution?.message}
            maxWidth={386}
          />
          <div className={classes.buttonContainer}>
            <Button
              type="button"
              className={`${classes.formButton} ${classes.previousButton}`}
              onClick={() => {
                const values = getValues()
                dispatch(setExperienceInfo(values))
                navigate('/onboarding/personal-info', { state: { avatar } })
              }}
            >
              Назад
            </Button>

            <Button className={classes.formButton} type="submit">
              Далее
            </Button>
          </div>
        </fieldset>
      </form>
    </>
  )
}

export default OnboardingFormExperience
